
$(document).ready(function(){

	/*
	* INICIA DADOS DE CONFIGURAÇÔES DE USUÁRIOS
	* INICIA DADOS DE CONFIGURAÇÔES DE USUÁRIOS
	*/
	$('.userModal').on('hidden.bs.modal', function (e) {
		$("#userForm input[name='id']").val('');
		$("#userForm input[name='name']").val('');
		$("#userForm input[name='email']").val('');
	})

	$('#btn-salvar-dados-user').click( function(e){
		var link = $('#userForm').attr('action');
		
		$.ajax({
			url: link,
			method: "POST",
			dataType: 'json',
			data: $('#userForm').serialize(),
			success: function( data ) {
				console.log( data );
				// invoca toast					
				successToast();	

				setTimeout(function(){
					location.reload();
				}, 3000);
			},
			error: function(response, status, error){
				// invoca toast de erro
				dangerToast();

				var data = $.parseJSON(response.responseText);
				console.log( data );

				$('#userForm').find('div.form-group').removeClass('has-error').find('.error-message').html('');
				
				$.each( data , function( index, value ) {
					if(index){
						$('#userForm .' + index + 'Container').addClass('has-error');
						$('#userForm .' + index + 'ContainerMessage').append( value );
					}
				});

				
			}
		})
		return false;
	});

	$('#btnAddUser').click(function(){
		// esconde informação do campo em branco
		$('#passInfo').css("display","none");
		$("#userForm input[name='email']").attr('readonly', false);
		$('#userForm').find('div.form-group').removeClass('has-error').find('.error-message').html('');
	})

	$('#userContainer .editButton').click( function(e){

		var id =  $(this).attr('id-user');	
		var link =  $(this).attr('href');

		// informa id editado
		$('#userId').val( id );

		// mostra informação do campo em branco
		$('#passInfo').css("display","block");
		
		// deixa email bloqueado para alteração
		$("#userForm input[name='email']").attr('readonly', true);

		// limpa qualquer erro 
		$('#userForm').find('div.form-group').removeClass('has-error').find('.error-message').html('');
					
		$.ajax({
			url: link,
			method: "GET",
			dataType: 'json',
			success: function( data ) {
				
				// abre modal com formulário
				$('.userModal').modal('toggle');

				// insere dados do usuário no formulário
				$("#userForm input[name='name']").val( data.name );
				$("#userForm input[name='email']").val( data.email );
				console.log( data );
			},
			error: function(){
				// invoca toast de erro
				dangerToast();
			}
		});

		return false;
	});

	$('#userContainer .deleteButton').click( function(){
		var id = $(this).attr('id-user');
		var link = $(this).attr('href');

		$.confirm({
			title: 'Atenção!',
			content: 'Tem certeza que deseja excluir este usuário?!',
			animation: 'zoom',
			closeAnimation: 'scale',
			buttons: {
				Sim: function () {

					$.ajax({
						url: link,
						method: "GET",
						dataType: 'json',
						success: function( data ) {
		
							if(data){
								$("#user-"+ id).fadeOut();
								// invoca toast					
								successToast();					
							}
							console.log(data);								
						},
						error: function(){
							// invoca toast de erro
							dangerToast();
						}
					});
					
				},
				Cancelar: {
					btnClass: 'btn-red any-other-class'
					
				}
			}
		});

		return false;
	});

	/*
	* FINALIZA DADOS DE CONFIGURAÇÔES DE USUÁRIOS
	* FINALIZA DADOS DE CONFIGURAÇÔES DE USUÁRIOS
	*/

	

	$('.form-reset').css({
		'display' : 'none',
		'position' : 'absolute',
		'opacity' : '0',
		'top' : '0',
		'width' : '350px',
		'margin-left': '-175px',
		'left' : '-350px;'
	});

	$('#recuperarSenha').click(function(){
		
		// $('.form-login').addClass('animate fadeInleft').css('display', 'none'); 
		$('.form-login').css({
			'opacity' : '0',
			'width' : '350px',
			'-webkit-transition': 'all .5s',
			'transition': 'all .5s',
			'margin-left': '300px'
		});

		$('.form-reset').css({
			'display' : 'block',
			'opacity' : '1',
			'width' : '350px',
			'-webkit-transition': 'all 1s',
			'transition': 'all 1s',
			'margin-left': '0px'
		});

		
	});

	$('#fazerLogin').click( function(){
		$('.form-reset').css({
			'opacity' : '0',
			'width' : '350px',
			'-webkit-transition': 'all .5s',
			'transition': 'all .5s',
			'margin-left': '-300px'
		});

		$('.form-login').css({
			'display' : 'block',
			'opacity' : '1',
			'width' : '350px',
			'-webkit-transition': 'all 1s',
			'transition': 'all 1s',
			'margin-left': '0px'
		});
	})

	$('#sendEmailResetForm').submit(function(){
		var link = $(this).attr('action');
		
		$.ajax({
			url: link,
			method: "POST",
			data: $(this).serialize(),
			dataType: 'json',
			success: function( data ) {

				if(data.success){
					$.confirm({
						title: 'Sucesso!',
						content: data.success,
						animation: 'zoom',
						closeAnimation: 'scale',
						buttons: {
							ok: function () {
								location.reload();
							}
						}
					});		
				}

				console.log(data);								
			},
			error: function(response, status, error){
				console.log(response.responseJSON.error);
				$.confirm({
					title: 'Houve um problema!',
					content: response.responseJSON.error,
					icon: 'fa fa-warning',
					type: 'red',
					typeAnimated: true,					
					buttons: {
						ok: function () {
							btnClass : 'btn-red'						
						}
					}
				});	
			}
		});

		return false;
	})

	$('.amount').mask('### ### ##0.00', {reverse: true});
})


// limpa todos os campos do formulário
function clearFormByID(id){
	$('#'+id)[0].reset();
	$('#'+ id +' select').val( null );
	$('#'+ id +' input[type="radio"]').attr('checked', false);
	$('#'+ id +' input[type="radio"]').iCheck('unchecked');
}


function successToast(){
	$.toast({
		text: "Solicitação concluída com sucesso!", // Text that is to be shown in the toast
		heading: 'Sucesso', // Optional heading to be shown on the toast
		icon: 'success', // Type of toast icon
		showHideTransition: 'slide', // fade, slide or plain
		allowToastClose: true, // Boolean value true or false
		hideAfter: 3500, // false to make it sticky or number representing the miliseconds as time after which toast needs to be hidden
		stack: 200, // false if there should be only one toast at a time or a number representing the maximum number of toasts to be shown at a time
		position: 'top-right', // bottom-left or bottom-right or bottom-center or top-left or top-right or top-center or mid-center or an object representing the left, right, top, bottom values
		
		textAlign: 'left',  // Text alignment i.e. left, right or center
		loader: true,  // Whether to show loader or not. True by default
		loaderBg: '#dddddd',  // Background color of the toast loader
		beforeShow: function () {}, // will be triggered before the toast is shown
		afterShown: function () {}, // will be triggered after the toat has been shown
		beforeHide: function () {}, // will be triggered before the toast gets hidden
		afterHidden: function () {}  // will be triggered after the toast has been hidden
	});	
}

function dangerToast(){
	$.toast({
		text: "Confira seus dados e tente novamente!", // Text that is to be shown in the toast
		heading: 'Houve algum problema', // Optional heading to be shown on the toast
		icon: 'error', // Type of toast icon
		showHideTransition: 'slide', // fade, slide or plain
		allowToastClose: true, // Boolean value true or false
		hideAfter: 3500, // false to make it sticky or number representing the miliseconds as time after which toast needs to be hidden
		stack: 200, // false if there should be only one toast at a time or a number representing the maximum number of toasts to be shown at a time
		position: 'top-right', // bottom-left or bottom-right or bottom-center or top-left or top-right or top-center or mid-center or an object representing the left, right, top, bottom values
		
		textAlign: 'left',  // Text alignment i.e. left, right or center
		loader: true,  // Whether to show loader or not. True by default
		loaderBg: '#dddddd',  // Background color of the toast loader
		beforeShow: function () {}, // will be triggered before the toast is shown
		afterShown: function () {}, // will be triggered after the toat has been shown
		beforeHide: function () {}, // will be triggered before the toast gets hidden
		afterHidden: function () {}  // will be triggered after the toast has been hidden
	});	
}

function mascaraValor(value) {
	return accounting.formatMoney(value, "R$ ", 2, ".", ",");
    // return value.formatMoney(2, ',', '.');
}

function mascaraDecimal(value) {
	return accounting.formatMoney(value, "", 2, "", ".");
    // return value.formatMoney(2, ',', '.');
}

